import React, {useState} from 'react'

import './App.css';


function App() {
  const[count, setCount]= useState(0)
  function addOne(){
    console.log('counter');
    setCount(count+1)
  }
  return (
  
    <div className="App">
     <h1>{count}</h1>
     <button onClick={addOne}>+</button>
    </div>
  );
}

export default App;
